import mediumZoom from 'medium-zoom';
import Loading from '@/components/Loading';
import Error from '@/components/Error';
export default {
    name: 'PageTwitter',
    components: {
        Loading,
        Error
    },
    head() {
        return Object.assign(this.$nuxtI18nSeo(), {
            title: `Twitter - ${process.env.title}`,
            meta: [
                {
                    hid: 'description',
                    name: 'description',
                    content: '我的 Twitter 动态'
                }
            ]
        });
    },
    data() {
        return {
            loading: false,
            error: null
        };
    },
    fetch({ store, params, redirect }) {
        if (process.server) {
            store.commit('SET_STORE', {
                key: 'ssr_init',
                value: true
            });
            return store.dispatch('getRss');
        }
    },
    mounted() {
        if (this.$store.state.ssr_init) {
            this.handleImage();
            return this.$store.commit('resetInit');
        }
        this.getData();
    },
    methods: {
        getData() {
            this.loading = true;
            this.$store
                .dispatch('getRss')
                .then((e) => {
                    this.loading = false;
                    this.handleImage();
                })
                .catch((err) => {
                    this.error = err;
                    this.loading = false;
                });
        },
        handleImage() {
            if (process.client) {
                this.$nextTick(() => {
                    const zoom = mediumZoom(
                        this.$refs['tweet-list'].querySelectorAll('img')
                    );
                    zoom.on('open', (event) => {
                        const el = event.target;
                        el.classList.remove('object-cover');
                        el.classList.remove('w-full');
                        el.classList.remove('h-full');
                    });
                    zoom.on('close', (event) => {
                        const el = event.target;
                        el.classList.add('object-cover');
                        el.classList.add('w-full');
                        el.classList.add('h-full');
                    });
                });
            }
        }
    }
};
